// qiankun

import { registerMicroApps, setDefaultMountApp, start } from "qiankun";

const loader = (loading: any) => {
  console.log(loading);
};

let styleMap = new Map<string, Element[]>();

registerMicroApps(
  [
    {
      name: "vue-ts", // 
      entry: "//localhost:4003", // 
      container: "#container", // 
      activeRule: "/vue", // xxx
      loader, // loader
      props: {
        routerBase: "/vue",
      },
    },
    {
      name: "vue-tsx", // 
      entry: "//localhost:4006", // 
      container: "#container", // 
      activeRule: "/vue-tsx", // xxx
      loader, // loader
      props: {
        routerBase: "/vue-tsx",
      },
    },
    {
      name: "vue-vite",
      entry: "//localhost:4004",
      container: "#container",
      activeRule: "/vue-vite",
      loader,
      props: {
        routerBase: "/vue-vite",
      },
    },
    {
      name: "react-ts",
      entry: "//localhost:4001",
      container: "#container",
      activeRule: "/react",
      loader,
      props: {
        routerBase: "/react",
      },
    },
    {
      name: "react-vite",
      entry: "//localhost:4002",
      container: "#container",
      activeRule: "/react-vite",
      loader,
      props: {
        routerBase: "/react-vite",
      },
    },
    {
      name: "html-music",
      entry: "//localhost:4005",
      container: "#container",
      activeRule: "/html-music",
      loader,
      props: {
        routerBase: "/html-music",
      },
    },
    {
      name: "html-study",
      entry: "//localhost:4007",
      container: "#container",
      activeRule: "/html-study",
      loader,
      props: {
        routerBase: "/html-study",
      },
    },
  ],
  {
    // 
    beforeLoad: (app: any) => {
      // 
      console.log("before load", app.name);
      Array.from(document.getElementsByTagName("head")[0].children)
        .filter((item) => item.tagName === "STYLE")
        .forEach((element) => {
          element.setAttribute("data-app", "main");
        });
      return Promise.resolve();
    },
    beforeMount: (app: any) => {
      console.log("", app.name);
      let appStyleList: Element[] = [];
      Array.from(document.getElementsByTagName("head")[0].children)
        .filter((item) => item.tagName === "STYLE")
        .forEach((element) => {
          let attr = element.getAttribute("data-app");
          if (!attr) {
            element.setAttribute("data-app", app.name);
            appStyleList.push(element);
            document
              .getElementsByTagName("qiankun-head")[0]
              .appendChild(element);
          }
          if (attr !== "main" && attr) {
            element.remove();
          }
        });

      let styleList = styleMap.get(app.name);
      if (styleList) {
        styleList.forEach((s) => {
          document.getElementsByTagName("qiankun-head")[0].appendChild(s);
        });
      } else {
        styleMap.set(app.name, appStyleList);
      }
      return Promise.resolve();
    },
    // qiankun  - 
    afterMount: (app: any) => {
      // 
      console.log("after mount", app.name);
      return Promise.resolve();
    },
    beforeUnmount: (app: any) => {
      console.log("", app.name);
      return Promise.resolve();
    },
    afterUnmount: (app: any) => {
      console.log("", app.name);
      return Promise.resolve();
    },
  }
);

// 
setDefaultMountApp('/vue');

// start
start({
  prefetch: true, // 
  sandbox: {
    experimentalStyleIsolation: true, //   ,
  },
});
